      <a :href="item.link" class="menu-itemsLink">
        <div class="menu--img">
          <img :src="require('../assets/images/nav-icons/' + item.img)">
        </div>
        <p> {{ item.text }} </p>
      </a>
    </article>
    <button class="menu-wrapper">
      <span></span>
      <span></span>
      <span></span>
    </button>
  </div>
</template>

<script>
import {mapState} from 'vuex'

export default {
  name: 'MenuItems',
  data() {
    return {
    }
  },
  computed: mapState([
    'menu'
  ])
}
</script>