<template>
  <section class="slider container">
    <ul class="slider--list">
        <li class="slider--item" v-for="(slide, key) in slider" :key="key" :class="{show: activeSlide === key}">
          <img class="slider--item__bg" :src="require('../assets/images/slider/slide' + (key+1) + '.png')" alt="">
          <div class="slider--item__content">
            <h2>{{ slide.title }}</h2>
            <p>{{ slide.text }}</p>
            <a :href="slide.link">СМОТРЕТЬ КАТАЛОГ</a>
          </div>
        </li>
    </ul>
    <article class="slider--btns">
      <button type="button" class="slider-btn slider-prev icon-chevron-left" @click="prevSlide"></button>
      <button type="button" class="slider-btn slider-next icon-chevron-right" @click="nextSlide"></button>
    </article>
  </section>
</template>

<script>
  import { mapState } from "vuex";

  export default {
    name: 'SliderItems',
    data() {
      return {
        activeSlide: 0,
        show: 'show'
      }
    },
    methods: {
      prevSlide() {
        this.activeSlide--
        console.log(this.activeSlide);
        if(this.activeSlide < 0) {
          this.activeSlide = this.slider.length - 1
        }
      },
      nextSlide() {
        this.activeSlide++
        console.log(this.activeSlide);
        if(this.activeSlide == this.slider.length){
          this.activeSlide = 0
        }
      },
    },
    computed: mapState([
      'slider'
    ])
  }
</script>