<template>

  <div class="map container">
    <router-view>
     <router-link to="/">Главная / </router-link> 
     <router-link to="/bag">Корзина</router-link> 
    </router-view>
  </div>

 <template v-if="productsInBag.length">
    <section class="basket container">
      <div class="basket-title">
        <p>Ваша корзина</p>
        <p>{{ productsInBag.length }} предмета</p>
      </div>
      <main class="basket-content">
        <article class="basket-item" v-for="(product,key) in productsInBag" :key="key">
          <img :src="require('../assets/images/items/' + product.img)" alt="">
          <div class="basket-item__content">
            <div class="basket-item__price">
              <p>{{product.name}}</p>
              <select class="basket-item__price--quantity">
                <option value="1">1</option>
                <option value="2">2</option>
                <option value="3">3</option>
              </select>
              <p>{{product.price}}₽</p>
            </div>
            <div class="basket-item__info">
              <div class="basket-item__info--item">
                <p>Цвет:</p>
                <span>Темно-синий</span>
                <div class="basket-item__info--item--color"></div>
              </div>
              <div class="basket-item__info--item">
                <p>Количество:</p>
                <select name="select">
                  <option value="1">1</option>
                  <option value="2">2</option>
                  <option value="3">3</option>
                </select>
              </div>
              <div class="basket-item__info--item">
                <p>Размер(Ш×Д×В):</p>
                <span>218 СМ x 95 СМ x 90 СМ</span>
              </div>
            </div>
          </div>
          <button class="basket-item__remove icon-close" @click="this.$store.dispatch('removeProduct', product.id)"></button>
        </article>
      </main>
      <div class="basket-total">
        <div class="basket-total__price">
          <span>Итоговая стоимость:</span>
          <p>{{ orderTotal() }}₽</p>
        </div>
        <a href="#!" class="add-btn">Оформить заказ</a>
      </div>
    </section>
 </template>
 
 <template v-else>
  <section class="empty-bag container">
    <h2>No products in bag</h2>
  </section>
 </template>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: 'v-basket',
  
  methods: {
    orderTotal() {
      var total = 0
      this.productsInBag.forEach(item => {
        total += item.price * item.quantity
      });
      return total.toFixed(2)
    }
  },
  computed: mapState([
    'products',
    'productsInBag'
  ])
}
</script>