<template>
  <section class="products container">
    <p class="section--title">Хиты продаж</p>
    <main class="products--content">
      <article class="products-card" v-for="(product, key) in products" :key="key">
        <div class="products-card__top">
          <div class="sale" v-if="(key+1) == 3">
            <img src="../assets/images/nav-icons/sale.svg" alt="">
            <span>-25%</span>
          </div>
          <a href="#!" class="icon-heart"></a>
        </div>
        <div class="products-card__info">
          <img :src="require('../assets/images/items/item' + (key+1) + '.png')" alt="">
          <h3>{{ product.name }}</h3>
          <small>{{ product.type }}</small>
          <p>{{ product.price }}₽</p>
        </div>
        <div class="products-card__bottom">
          <p>Размеры</p>
          <div class="products-card__dimentions">
            <div class="products-card__dimentions--item">
              <p>ШИРИНА</p>
              <span>{{product.width}}</span>
            </div>
            <div class="products-card__dimentions--item">
              <p>ГЛУБИНА</p>
              <span>{{product.depth}}</span>
            </div>
            <div class="products-card__dimentions--item">
              <p>ВЫСОТА</p>
              <span>{{product.height}}</span>
            </div>
          </div>
        </div>
        <button type="button" class="add-btn" v-if="!isInBag(product)" @click="addToBag(product)">Добавить в корзину</button>
        <button type="button" class="add-btn" v-else @click="this.$store.dispatch('removeProduct', product.id)">Убрать из корзины</button>
      </article>
    </main>
  </section>
</template>

<script>
  import { mapState } from 'vuex'
  
  export default {
    name: 'v-products',
    data() {
      return {}
    },
    methods: {
      addToBag(product) {
        this.$store.dispatch('addToBag', product)
      },
      isInBag(product) {
        return this.productsInBag.find(item => item.id == product.id)
      },
    },
    computed: mapState([
      'products',
      'productsInBag'
    ])
  }

</script>


