<template>
  <nav class="nav container">
    <article class="nav--logo">
      <div class="nav-burger" @click="this.active = !this.active">
        <span></span>
        <span></span>
        <span></span>
      </div>
      <router-view>
        <router-link to="/">
          <img src="../assets/images/logo.png" alt="logo">
        </router-link>
      </router-view>
    </article>
    <form action="#!" class="nav--form">
      <a href="#!" class="icon-search"></a>
      <input type="text" placeholder="Поиск">
    </form>
    <article class="nav--contacts">
      <a href="#!" class="icon-phone">8 (964) 89 99 119</a>
      <a href="#!" class="icon-delivery">Доставка</a>
    </article>
    <article class="nav--link__items">
      <router-view>
        <a href="#!" class="icon-heart"></a>
        <router-link  to="/bag" class="icon-cart"><span v-if="productsInBag.length">{{ productsInBag.length }}</span></router-link>
        <a href="#!" class="icon-user"></a>
      </router-view>
    </article>
  </nav>
  <div class="sidebar" :class="{active: this.active}">
  
    <div class="sidebar-top">
      <div class="sidebar-title">
        <h3>Меню</h3>
        <button class="close-btn icon-close" @click="this.active = false"></button>
      </div>
      <ul class="sidebar-list">
        <router-view>
          <li>
            <router-link to="/">
              <img src="../assets/images/nav-icons/main.svg" alt="">
              Главная
            </router-link>
          </li>
          <li>
            <router-link to="/about">
              <img src="../assets/images/nav-icons/about.svg" alt="">
              О нас
            </router-link>
          </li>
          <li>
            <router-link to="#!">
              <img src="../assets/images/nav-icons/contacts.svg" alt="">
              Контакты
           </router-link>
          </li>
        </router-view>
      </ul>
    </div>
    
    <div class="sidebar-main">
      <h3>Категории</h3>
      <ul class="sidebar-list">
        <li v-for="(category, key) in sidebarCategory" :key="key">
          <a href=category.link>
            <img :src="require('../assets/images/nav-icons/' + category.img)" alt="ctImg">
            {{ category.text }}
          </a>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import {mapState} from 'vuex'

export default {
  name: 'NavView',
  data() {
    return {
      active: false,
    }
  },
  methods: {
  },
  computed: mapState([
    'sidebarCategory',
    'productsInBag'
  ])
}
</script>